import BadRequestException from '../exceptions/BadRequestException';

class RegexHelper {
  // constructor() {}

  /**
   *    
   * @param {HTMLElement} field   DOM
   * @param {string} msg           
   */

  value(field, msg) {
    const content = field.value;

    if (content == undefined || content == null || (typeof content == 'string' && content.trim().length == 0)) {
      throw new BadRequestException(msg, field);
    }

    return true;
  }

  /**
   *    
   * @param {string} field    CSS 
   * @param {int} leg           
   * @param {string} msg           
   */
  maxLength(field, len, msg) {
    this.value(field, msg);

    const content = field.value;

    if (content.trim().length > len) {
      throw new BadRequestException(msg, field);
    }

    return true;
  }

  /**
   *    
   * @param {string} field    CSS 
   * @param {int} leg           
   * @param {string} msg           
   */
  minLength(field, len, msg) {
    this.value(field, msg);

    let content = field.value;

    if (content.trim().length < len) {
      throw new BadRequestException(msg, field);
    }

    return true;
  }

  /**
   *    
   *  @param {string} origin      CSS 
   *  @param {string} compare     CSS 
   *  @param {string} msg          
   */

  compareTo(origin, compare, msg) {
    this.value(origin, msg);
    this.value(compare, msg);

    var src = origin.value.trim(); // 
    var dsc = compare.value.trim(); //  

    if (src != dsc) {
      throw new BadRequestException(msg, origin);
    }

    return true;
  }

  /**
   *     
   * @param {string} field   CSS 
   * @param {string} msg          
   */

  check(field, msg) {
    const checkItem = Array.from(field).filter((v, i) => v.checked);

    if (checkItem.length == 0) {
      throw new BadRequestException(msg, field);
    }
  }

  /**
   *      
   * @param {string} field   CSS 
   * @param {string} msg          
   */

  checkMin(field, len, msg) {
    const checkItem = Array.from(field).filter((v) => v.checked);

    if (checkItem.length < len) {
      throw new BadRequestException(msg, field[0]);
    }
  }

  /**
   *      
   * @param {string} field   CSS 
   * @param {string} msg          
   */

  checkMax(field, len, msg) {
    const checkItem = Array.from(field).filter((v) => v.checked);

    if (checkItem.length > len) {
      throw new BadRequestException(msg, field[0]);
    }
  }

  /**
   *    
   * @param {string} field    CSS 
   * @param {string} msg        
   * @param {object} regexExpr  
   */

  field(field, msg, regexExpr) {
    this.value(field, msg);

    //    ?
    if (!regexExpr.test(field.value.trim())) {
      throw new BadRequestException(msg, field);
    }

    return true;
  }

  /**
   *     field()  .
   *  @param  {string} field   CSS 
   *  @param  {string} msg       
   */
  num(field, msg) {
    return this.field(field, msg, /^[0-9]*$/);
  }

  /**
   *     field()  .
   *  @param  {string} field   CSS 
   *  @param  {string} msg       
   */
  engNum(field, msg) {
    return this.field(field, msg, /^[a-zA-Z]*$/);
  }

  /**
   *     field()  .
   *  @param  {string} field   CSS 
   *  @param  {string} msg       
   */
  kor(field, msg) {
    return this.field(field, msg, /^[--]*$/);
  }

  /**
   *      field()  .
   *  @param  {string} field   CSS 
   *  @param  {string} msg       
   */
  engNum(field, msg) {
    return this.field(field, msg, /^[a-zA-Z0-9]*$/);
  }
  /**
   *      field()  .
   *  @param  {string} field   CSS 
   *  @param  {string} msg       
   */
  korNum(field, msg) {
    return this.field(field, msg, /^[--0-9]*$/);
  }

  /**
   *     field()  .
   *  @param  {string} field   CSS 
   *  @param  {string} msg       
   */
  email(field, msg) {
    return this.field(field, msg, /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i);
  }

  /**
   *      field()  .
   *  @param  {string} field   CSS 
   *  @param  {string} msg       
   */
  cellphone(field, msg) {
    return this.field(field, msg, /^01(?:0|1|[6-9])(?:\d{3}|\d{4})\d{4}$/);
  }

  /**
   *      field()  .
   *  @param  {string} field   CSS 
   *  @param  {string} msg       
   */
  telphone(field, msg) {
    return this.field(field, msg, /^\d{2,3}-\d{3,4}-\d{4}$/);
  }

  /**
   *           
   *  @param  {string} field   CSS 
   *  @param  {string} msg       
   */
  phone(field, msg) {
    this.value(field, msg);

    const content = field.value.trim();
    var check1 = /^01(?:0|1|[6-9])(?:\d{3}|\d{4})\d{4}$/;
    var check2 = /^\d{2,3}-\d{3,4}-\d{4}$/;

    if (!check1.test(content) && !check2.test(content)) {
      throw new BadRequestException(msg, field);
    }
    return true;
  }
}

export default new RegexHelper();
