import useAxios, { configure } from 'axios-hooks';
import styled from 'styled-components';
import Spinner from '../components/Spinner';
import Table from '../components/Table';
import regexHelper from '../components/libs/RegexHelper';

import axios from '../Axios';
import { useNavigate, useParams } from 'react-router-dom';
import dayjs from 'dayjs';
import { useCallback } from 'react';

const TableEx = styled(Table)`
  margin-top: 50px;
  margin-bottom: 15px;
  .inputWrapper {
    padding: 0;
    position: relative;
    text-align: left;
    .field {
      box-sizing: border-box;
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border: 0;
      padding: 0 10px;
      outline: none;
      font-size: 14px;
    }
    label {
      margin-left: 7px;
      margin-right: 10px;
      input {
        margin-right: 10px;
      }
    }
  }
`;

const ProfessorEdit = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  configure({ axios });
  const [{ data, loading: loading1, error }] = useAxios('/department');
  const [{ loading: loading2 }, refetch] = useAxios(
    {
      url: `/professor/${id}`,
      method: 'PUT',
    },
    { manual: true }
  );
  const [{ data: professor, loading: loading3, error2 }] = useAxios(`/professor/${id}`);

  const onSubmit = useCallback(
    (e) => {
      e.preventDefault();
      const current = e.target;

      try {
        regexHelper.value(current.name, ' ');
        regexHelper.kor(current.name, ' ');
        regexHelper.minLength(current.name, 2, ' 2  ');
        regexHelper.maxLength(current.name, 10, ' 10  ');
        regexHelper.value(current.userid, ' ');
        regexHelper.engNum(current.userid, '   ');
        regexHelper.minLength(current.userid, 2, ' 2  ');
        regexHelper.maxLength(current.userid, 20, ' 20  ');
        regexHelper.check(current.position, ' ');
        regexHelper.value(current.sal, ' ');
        regexHelper.num(current.sal, '  ');
        regexHelper.value(current.hiredate, ' ');
        regexHelper.value(current.comm, ' ');
        regexHelper.num(current.comm, '  ');
        regexHelper.value(current.deptno, ' ');
      } catch (e) {
        window.alert(e.message);
        e.field.focus();
        return;
      }
      let json = null;

      const name = current.name.value;
      const userid = current.userid.value;
      const position = current.position.value;
      const sal = current.sal.value;
      const hiredate = dayjs(current.hiredate.value).toISOString();
      const comm = parseInt(current.comm.value);
      const deptno = parseInt(current.deptno.value);

      (async () => {
        try {
          const res = await refetch({
            data: {
              name,
              userid,
              position,
              sal,
              hiredate,
              comm,
              deptno,
            },
          });
          json = res.data;
        } catch (e) {
          console.error(e);
        }
        if (json !== null) {
          window.alert('');
          navigate('/');
        }
      })();
    },
    [navigate, refetch]
  );

  return (
    <>
      <Spinner visible={loading1 || loading2 || loading3} />
      {error || error2 ? (
        <div>
          <h1>{error.code} Error</h1>
          <p>{error.message}</p>
        </div>
      ) : (
        !loading3 && (
          <form onSubmit={onSubmit}>
            <TableEx>
              <colgroup>
                <col width="120" />
                <col />
              </colgroup>
              <tbody>
                <tr>
                  <th></th>
                  <td className="inputWrapper">
                    <input className="field" type="text" name="name" defaultValue={professor.name} />
                  </td>
                </tr>
                <tr>
                  <th></th>
                  <td className="inputWrapper">
                    <input className="field" type="text" name="userid" defaultValue={professor.userid} />
                  </td>
                </tr>
                <tr>
                  <th></th>
                  <td className="inputWrapper">
                    <label>
                      <input type="radio" name="position" value="" defaultChecked={'' === professor.position} />
                      
                    </label>
                    <label>
                      <input type="radio" name="position" value="" defaultChecked={'' === professor.position} />
                      
                    </label>
                    <label>
                      <input type="radio" name="position" value="" defaultChecked={'' === professor.position} />
                      
                    </label>
                    <label>
                      <input type="radio" name="position" value="" defaultChecked={'' === professor.position} />
                      
                    </label>
                  </td>
                </tr>
                <tr>
                  <th></th>
                  <td className="inputWrapper">
                    <input className="field" type="number" name="sal" placeholder=" " defaultValue={professor.sal} />
                  </td>
                </tr>
                <tr>
                  <th></th>
                  <td className="inputWrapper">
                    <input className="field" type="date" name="hiredate" defaultValue={dayjs(professor.hiredate).format('YYYY-MM-DD')} />
                  </td>
                </tr>
                <tr>
                  <th></th>
                  <td className="inputWrapper">
                    <input className="field" type="number" name="comm" placeholder=" " defaultValue={professor.comm} />
                  </td>
                </tr>
                <tr>
                  <th></th>
                  <td className="inputWrapper">
                    <select name="deptno" className="field" defaultValue={professor.deptno}>
                      <option value=""> ------ </option>
                      {data &&
                        data.map(({ dname, id }) => {
                          return (
                            <option key={id} value={id}>
                              {dname}
                            </option>
                          );
                        })}
                    </select>
                  </td>
                </tr>
              </tbody>
            </TableEx>
            <div style={{ textAlign: 'center' }}>
              <button type="submit"></button>
            </div>
          </form>
        )
      )}
    </>
  );
};
export default ProfessorEdit;
